<template>
    <div id="Portfolio">
        <figure v-for="portfolio, index in shPortfolio">
            <h3>{{index}}</h3>
            <img :src="require('../assets/'+index+'.png')">
            <figcaption>
                <div class="btnGroup">
                    <a class="btn" id="btnApp" :href="'https://garlicchicken.github.io/'+index" target="_blank">APP</a>
                    <a class="btn" id="btnCode" :href="'https://github.com/garlicchicken/'+index" target="_blank"><i class="fas fa-code"></i></a>
                </div>
            </figcaption>
        </figure>
    </div>
</template>

<script>
export default {
    data(){
        return{
            shPortfolio : []
        }
    },
    created(){
        this.shPortfolio = {
          //  ->     '()' : ''
          'SNSLoginChat': 'SNS API   , Firebase    ',
          'ToDoManagement': 'Local Storage    , ,      ',
          'KakaoFriends':'kakao ix     KakaoFriends , (Mylist)   Main(index) Mylist     Mylist      .',
          'Weather':'Resource        ',
          'shEditor':'  HTML Web Editor'
        };
    }
}
</script>
<style>
    h3{
        color: rgb(78, 78, 78);
        text-align: center;
        margin: 5px 0px 15px 0px;
    }
    /* Portfolio CSS */
    #Portfolio{
        position: relative;
        max-width: 960px;
        column-width: 300px;
        column-gap: 20px;
        margin: auto;
    }
    #Portfolio figure{
        background-color: rgb(255, 255, 255);
        display: inline-block;
        margin: 10px 0px 10px 0px;
        padding: 10px 20px 10px 20px;
        opacity: 0.5;
    }
    #Portfolio figure:hover{
        opacity: 1;
    }
    #Portfolio figure img{
        display: inline-block;
        vertical-align: bottom;
        border-radius: 5px;
        padding-bottom: 0px;
        margin-bottom: 0px;
        width: 100%;
    }
    #Portfolio figure figcaption{
        font-family: 'Nanum Gothic', sans-serif;
        font-size: 0.9rem;
        color: #2b2b2b;
        text-align:justify;
        word-break:break-all;
        line-height: 1.4;
    }
    /* link(button) CSS */
    .btnGroup{
        border: #a0a0a0 1px;
        border-top-style: solid;
        margin: 12px 0px 8px 0px;
        padding: 0;
    }
    .btn{
        width:49%;
        text-decoration: none;
        text-align: center;
        padding:4px 0px 4px 0px;
        margin: 0;
        font-weight: bold;
        display:inline-block;
        transition:all 0.1s;
        background-color: #ffffff;
        color: #616161;
    }
    .btn:active{
        background-color: #616161;
        color: #ffffff;
    }
    .btn:hover{
        background-color: #616161;
        color: #ffffff;
    }
</style>


